<script>
import { onMounted } from "@vue/runtime-core";
export default {
  setup() {
    onMounted(() => {
      const path = document.querySelectorAll("#map svg path");
      console.log(path);
      path.forEach((n, i) => {
        if (i % 2 == 0) {
          n.setAttribute("data-aos", "fade-left");
          n.setAttribute("data-aos-duration", "2000");
          n.setAttribute("data-aos-delay", 10 * i);
        } else {
          n.setAttribute("data-aos", "fade-right");
          n.setAttribute("data-aos-duration", "2000");
          n.setAttribute("data-aos-delay", 10 * i);
        }
      });
      //  :data-aos-delay="500"
